import {useState} from 'react';
import '../App.css'
import 'antd/dist/antd.css';
import { Layout, Menu} from 'antd';
import { NotificationOutlined,DashboardOutlined,NumberOutlined,SettingOutlined,UserOutlined,DeploymentUnitOutlined } from '@ant-design/icons';
import Table from './Table'
import Tables from './Tables'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";



export default function Card()  {
  const { SubMenu } = Menu;
  const { Header, Content, Sider } = Layout;
    const [currentSelected, setselected] = useState('10')
    const [sidemenu , setsidemenu] = useState('20')
 return(
    <Router>
    <Layout className="layout">
    <Header className="header" >
      <div className="logo" />
      <Menu theme="dark" className="specialheader" mode="horizontal" defaultSelectedKeys={[currentSelected]} onSelect={(item) => setselected(item)}>
        <Menu.Item className="nav-item" key="10"><div className="reditem">
          <Link to="/">Home </Link>
          </div></Menu.Item>
        <Menu.Item className="nav-item" key="20"><div className="reditem">
        <Link to="/dashboard">Table1</Link> 
          </div></Menu.Item>
        <Menu.Item className="nav-item" key="30"><div className="reditem">
        <Link to="/">Table2 </Link> 
          </div></Menu.Item>
      </Menu>
    </Header>
    <Layout>
      <Sider width={250} className="site-layout-background">
        <Menu
          className="sidebar"
          mode="inline"
          defaultSelectedKeys={[sidemenu]}
          onSelect={(item) => setsidemenu(item)}
          defaultOpenKeys={[sidemenu]}
          
          style={{ height: '100%', borderRight: 0 }}
        >
          <Menu.Item key="20" icon={<DashboardOutlined />}>
           <Link to="/">Dashboard</Link> 
          </Menu.Item>
          
          <Menu.Item key="21" icon={<NumberOutlined />}>
          <Link to="/dashboard">Numbers</Link> 
            </Menu.Item>
          <Menu.Item key="22" icon={<DeploymentUnitOutlined />}>
          <Link to="/">Departments</Link>   
            </Menu.Item>
          <Menu.Item key="23" icon={<UserOutlined />}>
          <Link to="/">User</Link> 
            </Menu.Item>
          <Menu.Item key="24    " icon={<SettingOutlined />}>
          <Link to="/">Setting</Link> 
            </Menu.Item>

          <SubMenu key="sub1" icon={<NotificationOutlined />} title="Call History">
            <Menu.Item key="1">All calls</Menu.Item>
            <Menu.Item key="2">Missed calls</Menu.Item>
            <Menu.Item key="3">Recieved calls</Menu.Item>
            <Menu.Item key="4">Blocked calls</Menu.Item>
          </SubMenu>
        </Menu>
      </Sider>
    
        <Content
          className="site-layout-background"
          style={{
            padding: 24,
            margin: 0,
            minHeight: 280,
          }}>
            
         <Switch>
          <Route path="/" component={Table } exact>     
          </Route>
          <Route path="/dashboard"  component={Tables} exact>
          </Route>
        </Switch>

        </Content>
      </Layout>
    </Layout> 
    </Router>
 )
}
